import React, { forwardRef } from 'react';
import { elementType, node, string } from 'prop-types';
import classnames from 'classnames';
import Typography from '../typography';

const CSS_PREFIX = 'vds-footer-links';

const FooterLinks = forwardRef(({ children, className, tag, variant, ...remainingProps }, ref) => (
  <Typography
    className={classnames(CSS_PREFIX, className)}
    ref={ref}
    tag={tag}
    variant={variant}
    {...remainingProps}
  >
    {children}
  </Typography>
));

FooterLinks.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
  * Tag
  */
  tag: elementType,

  /**
   * Variant
   */
  variant: string
};

FooterLinks.defaultProps = {
  tag: 'p',
  variant: 'caption'
};

FooterLinks.displayName = 'FooterLinks';

export default FooterLinks;
